body {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100vh;
    margin: 0;
    background-color: black;
}

.toggle-container {
    text-align: center;
    display: flex;
    flex-direction: column;
    color: white; /* Ensures text color is white */
}

.system-status {
    font-size: 24px;
    margin-bottom: 20px;
    font-weight: bold;
    padding: 10px 20px;
    border-radius: 20px;
}

.system-status.online {
    color: #2ecc71; /* Green color for online status */
    border: 2px solid #2ecc71;
}

.system-status.offline {
    color: #e74c3c; /* Red color for offline status */
    border: 2px solid #e74c3c;
}

.toggle-btn {
    display: inline-block;
    padding: 20px 40px;
    margin: 5px;
    font-size: 16px;
    cursor: pointer;
    background-color: transparent;
    border: 2px solid white;
    color: #fff;
    border-radius: 30px;
    width: 100%;
}

.toggle-btn.active {
    background-color: #2ecc71;
}

.button-with-status {
    display: flex;
    align-items: center;
    margin: 5px;
}

.status-icon {
    margin-left: 10px;
    width: 30px;
    height: 40px;
    color: white;
}
